header {
	background: #070707;
	position  : fixed;
	top       : 0;
	width     : 100%;
	left      : 0;
	z-index   : 100;

	.container {
		padding : 20px 10px 0 10px;
		position: relative;
	}

	.col-no-rel {
		flex-basis: 0;
		flex-grow : 1;
		max-width : 100%;
	}

	.logo {
		@media (max-width: 1199.98px) {
			max-width: 180px;
			width    : 180px
		}

		img {
			width: 100%;
		}
	}

	.top-header {
		display    : flex;
		width      : 1000px;
		align-items: center;
		margin-left: auto;

		@media (max-width: 1365.98px) {
			width: 800px
		}

		@media (max-width: 1199.98px) {
			width: 750px;
		}

		@media (max-width: 1024.98px) {
			width: 100%;
		}

		.search {
			position   : relative;
			max-width  : 630px;
			flex       : 0 0 630px;
			margin-left: auto;

			@media (max-width: 1365.98px) {
				max-width: 430px;
				flex     : 0 0 430px;
			}

			@media (max-width: 1199.98px) {
				max-width: 400px;
				flex     : 0 0 400px;
			}

			input[type="text"] {
				width        : 100%;
				height       : 40px;
				border-radius: 5px;
				border       : none;
				padding      : 7px 15px;
				background   : #363636;
				color        : #fff;
			}

			button {
				display        : flex;
				align-items    : center;
				justify-content: center;
				position       : absolute;
				right          : 0;
				top            : 0;
				background     : #D82F25;
				border         : none;
				border-radius  : 0 5px 5px 0;
				height         : 40px;
				width          : 40px;
			}
		}

		.top-header-item {
			color      : #fff;
			font-size  : 10px;
			margin-left: 20px;
			flex       : 0 0 80px;
			max-width  : 80px;
			text-align : center;

			a {
				display       : flex;
				flex-direction: column;
				align-items   : center;
				color         : white;
				transition    : all .3s cubic-bezier(.2, .5, .5, .8);

				.text {
					p {
						color: #ffffff;
					}
				}
			}

			img {
				height: 24px;
			}

			span {
				margin-top : 6px;
				line-height: 1;
			}

			&:hover {
				cursor: pointer;

				>a {
					color: lighten(map-get($mau, main), 8%);
				}
			}

			@media (max-width: 1199.98px) {
				margin-left: 15px;
			}
		}

		.account-item {
			position: relative;

			@media (min-width: 1025px) {
				.mobile {
					display: none;
				}
			}

			.account-box {
				box-shadow   : 0 4px 10px rgba(0, 0, 0, .1);
				position     : absolute;
				top          : 100%;
				background   : #fff;
				width        : 290px;
				padding      : 10px;
				right        : 0;
				z-index      : 2;
				border-radius: 5px;
				border       : 1px solid #dfdfdf;
				opacity      : 0;
				visibility   : hidden;
				transform    : translateX(-15px);

				transition: all .3s cubic-bezier(.2, .5, .5, .8);

				.btn {
					border-radius: 7px;
				}

				.btn-register {
					margin-top: 10px;
				}
			}

			&:hover {
				.account-box {
					opacity   : 1;
					visibility: visible;
					transform : translateX(0);
				}
			}
		}

		.cart {
			display  : flex;
			position : relative;
			flex     : 0 0 50px;
			max-width: 50px;

			a {
				color    : #fff;
				font-size: 10px;
				display  : block;

				img {
					height : 24px;
					display: block;
					margin : 0 auto;
				}

				.text {

					span {
						display    : block;
						margin-top : 6px;
						line-height: 1;
					}

					p {
						position     : absolute;
						top          : 0;
						right        : 5px;
						display      : inline-block;
						line-height  : 1;
						text-align   : center;
						background   : #D82F25;
						border-radius: 50%;
						padding      : 5px;
					}
				}
			}
		}

		@media (min-width: 1025px) {
			.mobile-toggle {
				display: none;
			}
		}
	}

	.bottom-header {
		width      : 1000px;
		margin-left: auto;
		margin-top : 20px;

		.nav-list {
			display        : flex;
			justify-content: space-between;

			@media (max-width: 1199.98px) {
				font-size: 13px;
			}
		}

		.nav-item {
			display       : flex;
			align-items   : center;
			cursor        : pointer;
			padding-bottom: 32px;

			>svg {
				margin-right: 15px;
			}

			>a,
			>span,
			>h3 {
				color      : #ffffff;
				font-size  : inherit;
				font-weight: inherit;

				>h3 {
					font-size  : inherit;
					font-weight: inherit;
					color      : #ffffff;
				}
			}

			&:hover {
				>svg {
					path {
						fill: map-get($mau, main)
					}
				}

				>a,
				>span {
					color         : map-get($mau, main);
					// font-weight: 700;
				}
			}
		}

		.mega-menu {
			@media (min-width: 1025px) {
				.mega-menu-wrapper {
					box-shadow: 0 3px 6px rgba(black, .15);
					transform : translateX(-20px);
					opacity   : 0;
					visibility: hidden;
					position  : absolute;
					top       : 100%;
					background: #ffffff;
					left      : 10px;
					right     : 10px;
					z-index   : 11;
					height    : 525px;
				}
			}

			.list {
				position  : absolute;
				bottom    : 0;
				background: #1C1C1C;
				top       : 0;
				width     : 305px;
				padding   : 20px 0;
				z-index   : 2;
				overflow  : auto;

				// @media (min-width: 1025px) {
				// 	right: 50%;
				// }

				// @media (max-width: 1365.98px) {
				// 	right: 45%
				// }

				@media (max-width: 1199.98px) {
					// right: 40%;
					width   : 270px;
				}
			}

			.content {
				bottom: 0;
			}

			.mega-item {
				>a {
					line-height : 32px;
					display     : block;
					padding     : 0 35px;
					color       : white;
					border-right: 2px solid transparent;
				}

				@media (min-width: 1025px) {
					display: none;

					&[data-mega] {
						display: block;
					}

					&.active,
					&:hover {
						>a {
							background  : #444444;
							color       : map-get($mau, main);
							border-right: 2px solid map-get($mau, main);
						}
					}
				}

				&.mega-item-news {
					display: none;

					@media (max-width: 1024.98px) {
						display: block
					}
				}
			}

			.mega-child {
				background: #1C1C1C;
				height    : 100%;
				display   : flex;
				position  : absolute;
				top       : 0;
				left      : 0;
				width     : 100%;
				height    : 100%;
				z-index   : 1;

				.mega-img {
					max-width: 405px;
					flex     : 0 0 405px;

					@media (max-width: 1599.98px) {
						max-width: 345px;
						flex     : 0 0 345px;
					}

					@media (max-width: 1365.98px) {
						max-width: 310px;
						flex     : 0 0 310px;
					}

					p {
						font-size: 12px;
						color    : #ffffff;
						padding  : 14px 20px;
					}
				}

				.mega-content {
					max-width   : 100%;
					flex        : 0 0 100%;
					padding-left: 305px;
					background  : #ffffff;

					// @media (max-width: 1365.98px) {
					// 	max-width: 45%;
					// 	flex     : 0 0 45%;
					// }

					// @media (max-width: 1199.98px) {
					// 	max-width: 40%;
					// 	flex     : 0 0 40%;
					// }
					@media (max-width: 1024px) {
						padding-left: 0;
					}

					.mega-child-item-list,
					.mega-child-manufacture {
						padding: 30px;

						.mega-child-item-list-viewmore {
							@media (min-width: 1025px) {
								display: none;
							}
						}
					}

					.mega-child-manufacture {
						border-top: 1px solid #DFDFDF;

						h3 {
							text-transform : uppercase;
							text-decoration: underline;
							margin-bottom  : 15px;

							@media (max-width: 1024.98px) {
								font-weight: 400;
							}
						}
					}

					nav {
						display  : flex;
						flex-wrap: wrap;

						a {
							flex         : 0 0 auto;
							width        : auto;
							max-width    : 100%;
							min-height   : 26px;
							padding      : 7px 14px;
							border-radius: 13px;
							border       : 1px solid #DFDFDF;
							margin-right : 5px;
							margin-bottom: 5px;
							transition   : all .3s cubic-bezier(.2, .5, .5, .8);
							display      : flex;
							align-items  : center;

							&:hover {
								border: 1px solid map-get($mau, main);
								color : map-get($mau, main);
							}
						}
					}
				}

				&.mega-child-news {
					display: none;

					@media (max-width: 1024.98px) {
						display: flex;

						nav {
							a {
								width: 100%;
							}
						}
					}
				}
			}

			@media (min-width: 1025px) {
				&:hover {
					.mega-menu-wrapper {
						transform : translateX(0);
						transition: all .3s cubic-bezier(.2, .5, .5, .8);
						opacity   : 1;
						visibility: visible;

					}
				}
			}
		}

		@media (max-width: 1365.98px) {
			width: 800px
		}

		@media (max-width: 1199.98px) {
			width: 750px;
		}

		@media (max-width: 1024.98px) {
			width: 100%;
		}
	}

	@media (max-width: 1024.98px) {
		.container {
			padding: 20px 10px 0;
		}

		.logo {
			position : absolute;
			width    : 160px;
			left     : 50%;
			transform: translateX(-50%);
			top      : 0;
			z-index  : 2;
		}

		.top-header {
			padding-top: 50px;
			width      : 100%;

			.search {
				margin-left: 0;
				flex       : 0 0 100%;
				max-width  : 100%;
			}

			.mobile-toggle {
				position   : absolute;
				top        : 20px;
				left       : 15px;
				height     : 40px;
				align-items: center;
				z-index    : 2;
				margin-left: 0;
			}

			.cart {
				position   : absolute;
				right      : 15px;
				top        : 20px;
				height     : 40px;
				z-index    : 2;
				align-items: center;

				a {
					.text {
						span {
							display: none;
						}

						p {
							width : 16px;
							height: 16px;
							right : -5px;
						}
					}
				}
			}

			.top-header-item {

				&.cart,
				&.mobile-toggle {
					display: flex;
				}
			}
		}

		.bottom-header {
			max-width : 100%;
			margin-top: 10px;

			.nav-list {
				flex-wrap      : wrap;
				justify-content: flex-start;
				margin         : 0 -2.5px;

				.nav-item {
					padding-bottom: 0;
					padding       : 0 2.5px;
					margin-bottom : 5px;
					flex          : 0 0 25%;
					max-width     : 25%;
					display       : none;

					@media (max-width: 767.98px) {
						flex     : 0 0 33.333%;
						max-width: 33.333%;
					}

					@media (max-width: 575.98px) {
						flex     : 0 0 50%;
						max-width: 50%;
					}

					>a {
						height         : 30px;
						font-size      : 10px;
						display        : block;
						border         : 1px solid map-get($mau, main);
						width          : 100%;
						display        : flex;
						justify-content: center;
						align-items    : center;
						border-radius  : 5px;
					}
				}

				.mega-menu {
					flex     : 0 0 0;
					max-width: 0px;
					padding  : 0 0;
					display  : flex;

					>svg,
					>span,
					>h3 {
						display: none;
					}
				}
			}

			.mega-menu {
				.mega-menu-wrapper {
					position   : fixed;
					top        : 0px;
					width      : 100%;
					background : white;
					z-index    : 10;
					bottom     : 0;
					left       : 0;
					max-width  : 375px;
					display    : flex;
					z-index    : 21;
					align-items: flex-start;
					transform  : translateX(-100%);
					transition : all .3s cubic-bezier(.2, .5, .5, .8);

					.btn-close {
						display      : flex;
						position     : absolute;
						top          : 0;
						right        : 0;
						align-items  : center;
						z-index      : 3;
						padding-right: 10px;
						padding-left : 10px;
						height       : 70px;
					}

					.account-item {
						flex        : 0 0 100%;
						max-width   : 100%;
						z-index     : 3;
						height      : 70px;
						background  : #1C1C1C;
						display     : flex;
						align-items : center;
						padding-left: 10px;
						position    : relative;

						.desktop {
							display: none;
						}

						a {
							span {
								padding-left: 10px;
								color       : white;
								font-size   : 14px;
							}
						}

						.account-box {
							display   : none;
							position  : absolute;
							border    : 1px solid #DFDFDF;
							top       : 100%;
							left      : 0;
							width     : 100%;
							padding   : 10px;
							background: white;

							.btn-register {
								margin-top: 10px;
							}

							&.active {
								display: block;
							}
						}
					}

					&.active {
						transform: translate(0);
					}
				}

				.mega-item {
					position: relative;

					&:after {
						content   : '';
						position  : absolute;
						height    : 1px;
						left      : 15px;
						right     : 0;
						background: #DFDFDF;
						bottom    : 0;
					}

					>a {
						padding    : 0 15px;
						color      : #1C1C1C;
						min-height : 45px;
						line-height: 1.4;
						display    : flex;
						align-items: center;
					}

					&.active {
						>a {
							background : #ffffff;
							color      : map-get($mau, main);
							font-weight: 700;
						}

						&:after {
							content: none;
						}
					}

				}

				.list {
					padding   : 0;
					flex      : 0 0 140px;
					max-width : 140px;
					top       : 70px;
					left      : 0;
					background: #F5F5F5;
				}

				.content {
					position: absolute;
					right   : 0;
					left    : 140px;
					top     : 70px;
				}

				.mega-child {
					.mega-img {
						display: none;
					}

					.mega-content {
						margin-left: 0;
						flex       : 0 0 100%;
						max-width  : 100%;
						overflow   : auto;

						.mega-child-item-list,
						.mega-child-manufacture {
							padding: 10px;

							.mega-child-item-list-viewmore {
								display        : block;
								text-align     : center;
								text-decoration: underline;
								color          : map-get($mau, main);
								margin-top     : 10px;
							}
						}

						nav {
							a {
								padding: 0 7px;
							}
						}
					}
				}
			}
		}
	}
}

.fancybox-slide--html {
	padding: 15px;
}

.account-popup {
	width        : 100%;
	max-width    : 780px;
	border-radius: 5px;
	overflow     : visible;

	.fancybox-close-small {
		background: black;
		padding   : 0;
		top       : -17px;
		right     : -17px;
		width     : 34px;
		height    : 34px;
		opacity   : 1;

		svg {
			path {
				fill: white;
			}
		}

		@media (max-width: 1024.98px) {

			top   : -12px;
			right : -12px;
			width : 24px;
			height: 24px;
		}
	}

	.section-title {
		margin-bottom: 10px;
		font-size    : 21px;
		font-weight  : 700;

		@media (max-width: 1024.98px) {
			text-align: center;
			font-size : 16px;
		}
	}

	.popup-wrapper {
		margin   : -44px;
		display  : flex;
		flex-wrap: wrap;

		@media (max-width: 767.98px) {
			padding: 0 40px;
		}
	}

	.left {
		flex      : 0 0 375px;
		max-width : 375px;
		padding   : 60px 35px;
		background: #F6F6F6;

		.imgbox {
			margin-bottom: 20px;
		}

		@media (max-width: 1024.98px) {

			.imgbox,
			p {
				display: none;
			}
		}

		@media (max-width: 767.98px) {
			flex      : 0 0 100%;
			max-width : 100%;
			padding   : 0;
			background: white;
		}

		p {
			margin-bottom: 10px;
		}
	}

	.right {
		flex-basis: 0;
		flex-grow : 1;
		max-width : 100%;
		padding   : 60px;

		@media (max-width: 767.98px) {
			flex     : 0 0 100%;
			max-width: 100%;
			padding  : 0 0;
		}

		.right-wrapper {
			overflow      : hidden;
			display       : flex;
			align-items   : center;
			flex-direction: column;
		}
	}

	.social {
		display       : flex;
		flex-direction: column;
		align-items   : center;
		overflow      : hidden;
		width         : 100%;

		.btn {
			margin-bottom   : 10px;
			// display      : flex;
			// height       : 40px;
			// border-radius: 5px;
			width           : 100%;
			// color        : white;
			// font-size    : 14px;

			// img {
			// 	flex     : 0 0 42px;
			// 	max-width: 42px;
			// 	height   : 18px;
			// }

			// span {
			// 	flex-basis : 0;
			// 	flex-grow  : 1;
			// 	max-width  : 100%;
			// 	text-align : center;
			// 	line-height: 25px;
			// 	border-left: 1px solid white;
			// }

			// &.btn-google {
			// 	background: #DB4437;
			// }

			// &.btn-facebook {
			// 	background: #3C5A99;
			// }

			&:last-child {
				margin-bottom: 0;
			}
		}
	}

	p.seperate {
		position      : relative;
		padding-bottom: 0;
		margin-top    : 10px;
		margin-bottom : 10px;

		&::before {
			position   : absolute;
			top        : 50%;
			height     : 1px;
			width      : 300px;
			left       : 100%;
			margin-left: 10px;
			background : #DFDFDF;
			content    : ''
		}

		&::after {
			position    : absolute;
			top         : 50%;
			height      : 1px;
			width       : 300px;
			right       : 100%;
			margin-right: 10px;
			background  : #DFDFDF;
			content     : ''
		}
	}

	.form-option,
	.bottom-ask {
		display    : flex;
		align-items: center;
		width      : 100%;

		a {
			margin-left    : auto;
			font-size      : 12px;
			color          : #3C5A99;
			text-decoration: underline;
		}

		p {
			font-size : 12px;
			margin-top: 12px;
			width     : 100%;
			text-align: center;

			a {
				color          : map-get($mau, main);
				text-decoration: none;
			}
		}
	}

	form {
		width: 100%;

		input[type="text"],
		input[type="password"] {
			height       : 40px;
			border-radius: 5px;
			margin-bottom: 10px;
			border       : 1px solid #888888;
			width        : 100%;
			padding      : 0 12px;
			color        : #1C1C1C;

			&::placeholder {
				color: #1C1C1C;
			}
		}

		.form-check {
			label {
				font-size     : 12px;
				padding-left  : 18px;
				position      : relative;
				cursor        : pointer;
				color         : #1C1C1C;
				letter-spacing: -.5px;

				a {
					color          : map-get($mau, main);
					text-decoration: none;
				}

				&:before {
					content        : '';
					width          : 13px;
					height         : 13px;
					border-radius  : 3px;
					border         : 1px solid #888888;
					position       : absolute;
					top            : 1px;
					left           : 0;
					display        : flex;
					justify-content: center;
					align-items    : center;
					font-size      : 16px;
				}
			}

			input {
				display: none;

				&:checked~label:before {
					border     : 1px solid map-get($mau, main);
					color      : map-get($mau, main);
					content    : '\f12c';
					font-family: "Material Design Icons";
				}
			}
		}

		.form-button {
			margin-top: 25px;

			button {
				height       : 40px;
				border-radius: 15px;
				background   : map-get($mau, main);
				color        : white;
				border       : 0;
				padding      : 0 12px;
				width        : 100%;
			}
		}
	}

	@media (max-width: 767.98px) {

		.popup-wrapper {
			margin: 0 -44px;

			img.logo {
				display: none;
			}

			.left {
				margin-bottom: 15px;
			}
		}
	}
}

.account-popup.forgot-password {
	max-width: 400px;

