header {
	.login {
		position: relative;

		.login-popup {
			display      : none;
			position     : absolute;
			width        : 270px;
			padding      : 15px;
			border-radius: 5px;
			background   : white;
			top          : 100%;
			margin-top   : 15px;
			left         : 0;
			z-index      : 2;
			box-shadow   : 0 4px 10px rgba(black, .1);
			border       : 1px solid #DFDFDF;

			@media (max-width: 1024.98px) {
				margin-top: 0;
				width     : 310px;
			}

			&.open {
				display: block;
			}

			&:before {
				content      : '';
				position     : absolute;
				bottom       : 100%;
				left         : 10px;
				border-left  : 10px solid transparent;
				border-right : 10px solid transparent;
				border-bottom: 10px solid white;
				z-index      : 2;
			}

			&:after {
				content      : '';
				position     : absolute;
				bottom       : 100%;
				left         : 9px;
				border-left  : 11px solid transparent;
				border-right : 11px solid transparent;
				border-bottom: 11px solid #DFDFDF;
			}

			.btn {
				margin-bottom: 10px;
			}

			p {
				margin-bottom: 10px;
			}

			.btn-register {
				height    : 40px;
				background: #F5F5F5;
				color     : #1c1c1c;
			}
		}
	}
}


header {
	@media (max-width: 1024.98px) {
		.top-header {
			.top-header-item {
				display: none;
			}
		}
	}

	&.active {

		@media (max-width: 1024.98px) {
			.logo {
				display: none;
			}

			.bottom-header .nav-list .nav-item {
				&:not(.mega-menu) {
					display: none;
				}
			}

			.top-header {
				.search {
					position: absolute;
					top     : 20px;
					left    : 55px;
					right   : 50px;
					z-index : 2;
				}
			}
		}

		@media (min-width: 1025px) {
			.logo {
				display: none;
			}

			.bottom-header .nav-list {
				.nav-item {
					display: none;
				}

				.mega-menu {
					position       : absolute;
					top            : 20px;
					height         : 40px;
					left           : 10px;
					background     : #ffffff;
					display        : flex;
					justify-content: center;
					align-items    : center;
					padding-bottom : 0;
					width          : 230px;
					border-radius  : 5px;

					@media (max-width: 1199.98px) {
						width: 210px;
					}

					>svg {
						path {
							fill: #1c1c1c;
						}
					}


					>span,
					>h3 {
						color: #1c1c1c;
					}

					&:hover {
						background: map-get($mau, main);

						>svg {
							path {
								fill: #ffffff;
							}
						}

						>span,
						>h3 {
							color: #ffffff;
						}

						.mega-menu-wrapper {
							width: 970px;
							left : 0;

							@media (min-width: 1200px) {
								width: 1120px;
							}

							@media (min-width: 1360px) {
								width: 1300px;
							}

							@media (min-width: 1600px) {
								width: 1410px;
							}
						}
					}
				}
			}
		}
	}
}

.home-page {
	header {

		@media (max-width: 1024.98px) {
			.bottom-header {
				.nav-list {
					.nav-item {
						&:not(.mega-menu) {
							display: flex;
						}
					}
				}
			}

			.top-header-item {
				display: none;

				&.cart,
				&.mobile-toggle {
					display: flex;
				}
			}

			&.active {
				.bottom-header .nav-list .nav-item {
					&:not(.mega-menu) {
						display: none;
					}
				}
			}
		}
	}

	main {
		@media (max-width: 1024.98px) {
			padding-top: 190px;
		}

		@media (max-width: 575.98px) {
			padding-top: 225px;
		}
	}
}