.home-banner {
	margin-top: 15px;

	.slider-HomeBanner {
		figure {
			position: relative;

			img {
				width: 100%;
			}

			figcaption {
				position: absolute;
				color: #9B9B9B !important;
				top: 55px;
				left: 70px;

				span {
					font-size: 18px;
				}

				h5 {
					font-size: 33px;
					font-weight: 700;
				}

				.see-more {
					display: inline-block;
					font-size: 12px;
					line-height: 36px;
					font-weight: 700;
					color: #D82F25;
					background: #FFFFFF;
					border: 1px solid #D82F25;
					padding: 0 40px;
					border-radius: 25px;
					margin-top: 30px;
				}
			}
		}

		.swiper-slide {
			a {
				display: block;

				img {
					width: 100%;
				}
			}

