.list-same-news {
	background: transparent;
	padding   : 30px 0 0 0;

	.catalog-news {
		margin-bottom: 15px;

		h3 {
			font-size: 24px;
			color    : #1c1c1c;
		}

		P {
			display    : flex;
			align-items: center;

			span {
				font-size: 14px;
				color    : #888888;
			}
		}
	}

	.block-news {
		border          : #aaaaaa solid 1px;
		padding         : 15px;
		display         : flex;
		flex-wrap       : wrap;
		margin-bottom   : 20px;
		background-color: white;

		.content {
			display       : flex;
			flex-direction: column;
			flex-basis    : 0;
			flex-grow     : 1;
			max-width     : 100%;
			padding-left  : 30px;

			h5 {
				font-size         : 13px;
				font-weight       : 700;
				color             : #1c1c1c;
				margin-bottom     : 10px;
				-webkit-line-clamp: 2;
				-webkit-box-orient: vertical;
				overflow          : hidden;
				display           : -webkit-box;
				text-overflow     : ellipsis;
			}

			p {
				font-size         : 12px;
				color             : #888888;
				overflow          : hidden;
				text-overflow     : ellipsis;
				display           : -webkit-box;
				-webkit-line-clamp: 2;
				-webkit-box-orient: vertical;
			}

			.time {
				position    : relative;
				margin-top  : auto;
				padding-left: 25px;

				span {
					color      : #888888;
					font-size  : 11px;
					line-height: 1;
				}

				&::before {
					position        : absolute;
					content         : "";
					background-image: url(../img/icons/thoigiantintuc.svg);
					background-size : cover;
					left            : 0;
					top             : 8px;
					width           : 13px;
					height          : 13px;
				}
			}
		}

		.img-news {
			flex     : 0 0 40%;
			max-width: 120px;
			height   : 120px;

			img {
				width     : 100%;
				object-fit: cover;
			}
		}
	}

	@media (max-width: 1024.98px) {
		background: #ffffff;
		padding   : 15px 0;

		.block-title {
			.main-title {
				padding-bottom: 0;
			}
		}

		.catalog-news {
			margin-bottom: 10px;

			&.mt-mb-20 {
				margin-top: 0;
			}
		}
	}

	@media (max-width: 575.98px) {
		.block-news {
			padding      : 10px;
			margin-bottom: 10px;

			.content {
				padding-left: 20px;

				h5 {
					font-size: 14px;
				}
			}

			.img-news {
				flex       : 0 0 40%;
				max-width  : 210px;
				margin-left: auto;

				img {
					width     : 100%;
					object-fit: cover;
				}
			}
		}
	}
}